<template>
  <div
    class="my-notice-container"
    :class="{ 'tran-wrapper': isShow }"
  >
    <div class="flex justify-between title-wrapper">
      <span class="title text-bold"></span>
      <span
        class="el-icon-close text-lg"
        @click="close"
      ></span>
    </div>
    <div class="content-wrapper">
       vue admin work 
    </div>
    <div class="padding-tb">
      <el-button
        size="mini"
        type="primary"
        icon="el-icon-shopping-cart-full"
        @click="showGetSource"
      ></el-button>
      <el-button
        size="mini"
        type="danger"
        icon="el-icon-setting"
        @click="showProjectInfo"
      ></el-button>
      <el-button
        size="mini"
        icon="el-icon-chat-dot-square"
        @click="showContact"
      ></el-button>
    </div>
    <Dialog
      ref="projectInfoDialog"
      title="vue-admin-work"
    >
      <template>
        <ProjectInfo />
      </template>
    </Dialog>
    <Dialog
      ref="contactDialog"
      title=""
    >
      <template>
        <div style="width: 60%; margin: 0 auto;">
          <Contact />
        </div>
      </template>
    </Dialog>
    <Dialog
      ref="getSourceDialog"
      title=""
    >
      <template>
        <GetSource />
      </template>
    </Dialog>
  </div>
</template>

<script>
import ProjectInfo from './ProjectInfo'
import Contact from './Contact'
import GetSource from './GetSource'
export default {
  name: 'MyNotice',
  components: { ProjectInfo, Contact, GetSource },
  data() {
    return {
      isShow: false
    }
  },
  mounted() {
    setTimeout((_) => {
      this.isShow = true
    }, 1500)
  },
  methods: {
    showGetSource() {
      this.close()
      this.$refs.getSourceDialog.show()
    },
    showProjectInfo() {
      this.close()
      this.$refs.projectInfoDialog.show()
    },
    showContact() {
      this.close()
      this.$refs.contactDialog.show()
    },
    show() {
      this.isShow = true
    },
    close() {
      this.isShow = false
    }
  }
}
</script>

