export default {
  data() {
    return {
      userPasswordModel: {
        originPassword: '',
        newPassword: '',
        confirmPassword: ''
      }
    }
  },
  methods: {
    validatePassword() {
      if (!this.userPasswordModel.originPassword) {
        this.$errorMsg('')
        return
      }
      if (!this.userPasswordModel.originPassword) {
        this.$errorMsg('')
        return
      }
      if (!this.userPasswordModel.confirmPassword) {
        this.$errorMsg('')
        return
      }
      return true
    }
  }
}
